import React from 'react'
import { Redirect, RedirectProps } from 'react-router-dom'

import { resolveLocation } from './RelativeLink'

interface IProps extends RedirectProps {}

export const RelativeRedirect: React.FC<IProps> = ({ to, from, ...props }) => (
  <Redirect
    to={resolveLocation(to, from || `${location.pathname}/`)}
    from={from}
    {...props}
  />
)
