import React from 'react'
import { Field, FieldType, IFieldApi } from 'react-form'

import { RM } from '../helpers/ramda'
import { i18n } from '../i18n'
import { ISelectControlFlatProps, SelectControlFlat } from './SelectControlFlat'

interface IFormInputSelectProps<ItemType>
  extends Omit<
      ISelectControlFlatProps<ItemType>,
      | 'selectedItems'
      | 'onMultiSelection'
      | 'selectedItem'
      | 'onSingleSelection'
    > {
  field: FieldType
  fieldIsPure?: boolean
  getItemId?: (item: ItemType) => ID
  isRequired?: boolean
  validationName?: string
  defaultSelection?: ReadonlyArray<ItemType> | ItemType
}

export class FormInputFlatSelect<ItemType> extends React.Component<
  IFormInputSelectProps<ItemType>
> {
  static defaultProps = {
    validationName: i18n.t`Value`,
    getItemId: RM.prop('id'),
  }
  validate = value => {
    const { isRequired, isMulti, validationName } = this.props
    if (isRequired) {
      if (isMulti && (!Array.isArray(value) || value.length === 0)) {
        return i18n.t`${validationName} must have at least option selected`
      }
      if (!isMulti && (value === null || value === undefined)) {
        return i18n.t`${validationName} must have a selection`
      }
    }
    return null
  }
  getControlProps(
    fieldApi: IFieldApi,
  ): Partial<ISelectControlFlatProps<ItemType>> {
    const { isMulti, items, defaultSelection, getItemId } = this.props

    const selection = fieldApi.value || defaultSelection

    if (isMulti) {
      const isSelected = (item: ItemType) =>
        selection.includes(getItemId!(item))
      return {
        selectedItems: Array.isArray(selection) ? items.filter(isSelected) : [],
        onMultiSelection: selectedItems => {
          fieldApi.setValue(selectedItems.map(getItemId!))
          fieldApi.setTouched(true)
        },
      }
    }

    return {
      selectedItem:
        selection && items.find(item => getItemId!(item) === selection),
      onSingleSelection: item => {
        fieldApi.setValue(item ? getItemId!(item) : null)
        fieldApi.setTouched(true)
      },
    }
  }
  render() {
    const {
      field,
      fieldIsPure = false,
      isRequired = false,
      isClearable = false,
      ...props
    } = this.props
    return (
      <Field field={field} pure={fieldIsPure} validate={this.validate}>
        {fieldApi => (
          <SelectControlFlat<ItemType>
            {...this.getControlProps(fieldApi)}
            isInvalid={Boolean(fieldApi.error)}
            isClearable={isClearable && !isRequired}
            {...props}
          />
        )}
      </Field>
    )
  }
}
