import React from 'react'
import styled from 'styled-components'

interface IProps {
  pictogramId: string
  fontSize?: number
  fontSizeUnit?: string
}

const PictogramDisplayStyled = styled.div`
  &:before {
    font-size: ${getFontSize} !important;
    margin-left: 0.5rem !important;
  }
`

export const PictogramDisplay: React.FC<IProps> = ({
  pictogramId,
  fontSize = 2,
  fontSizeUnit = 'rem',
}) => (
  <PictogramDisplayStyled
    className={getClassName(pictogramId)}
    fontSize={fontSize}
    fontSizeUnit={fontSizeUnit}
  />
)

function getFontSize({ fontSize = 2, fontSizeUnit = 'rem' }: Partial<IProps>) {
  return `${fontSize}${fontSizeUnit}`
}

function getClassName(pictogramId: string) {
  return `flaticon-${pictogramId}`
}
