import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { useQuery } from 'react-apollo-hooks'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { QMenuGuideDownload } from '../../../graph/types'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { MenuGuideDownloadQuery } from './MenuGuideDownload.graphql'

const MenuGuideDownloadButton = styled(TouchButton)`
  white-space: nowrap;
  margin-left: 2rem;
  & a {
    text-decoration: none;
    color: ${Colors.black};
    padding: 1rem;
  }
`

export const MenuGuideDownload = observer(() => {
  const { settings } = useRoot()
  const { loading, data } = useQuery<
    QMenuGuideDownload.Query,
    QMenuGuideDownload.Variables
  >(MenuGuideDownloadQuery, {
    suspend: false,
    variables: { branchId: settings.branchId },
  })
  if (
    loading ||
    !(data && data.companyBranch && data.companyBranch.foodMenuUrl)
  ) {
    return null
  }
  return (
    <MenuGuideDownloadButton color={Colors.greyMiddle}>
      <a href={data.companyBranch.foodMenuUrl} target="_blank">
        <Trans>Menu codes manual</Trans>
      </a>
    </MenuGuideDownloadButton>
  )
})
