import React from 'react'
import { TableCellProps } from 'react-virtualized'
import styled from 'styled-components'

import { Column, DataTable } from '../../../atoms/DataTable'
import { DataTableContainer } from '../../../atoms/DataTableContainer'
import { QMenuStockDepositStockItems } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'

interface IProps {
  searchValue: string
  stockItems: QMenuStockDepositStockItems[]
  onSelect(stockItem: QMenuStockDepositStockItems): void
}

export const StockDepositResultTable: React.FC<IProps> = ({
  stockItems,
  searchValue,
  onSelect,
}) => (
  <DataTableContainer>
    <DataTable items={stockItems} heightStretch={false}>
      <Column
        dataKey="id"
        width={Spacing.ButtonColumnWidthPx}
        minWidth={Spacing.ButtonColumnWidthPx}
        cellRenderer={ColumnRendererSelect}
        columnData={{ onSelect }}
      />
      <Column
        dataKey="quantity"
        width={100}
        label={i18n.t`Card stock`}
        cellRenderer={stockWithUnit}
      />
      <Column
        dataKey="code"
        width={100}
        label={i18n.t`Card code`}
        cellRenderer={highlightMatch(searchValue)}
      />
      <Column
        dataKey="nameLabel"
        width={300}
        label={i18n.t`Card name`}
        cellRenderer={highlightMatch(searchValue)}
      />
      <Column dataKey="note" width={300} label={i18n.t`Card note`} />
    </DataTable>
  </DataTableContainer>
)

function ColumnRendererSelect({
  rowData,
  columnData: { onSelect },
}: TableCellProps) {
  return (
    <TouchButton
      iconName="done"
      width={Spacing.ButtonHeight}
      height={Spacing.ButtonHeight}
      onClick={() => onSelect(rowData)}
    />
  )
}

const HighlightedValue = styled.span`
  background-color: ${Colors.yellow};
`

function highlightMatch(searchValue: string) {
  return ({ cellData }: TableCellProps) => {
    const index = cellData
      .toLocaleLowerCase()
      .indexOf(searchValue.toLocaleLowerCase())
    if (index === -1) {
      return cellData
    }
    const begin = cellData.substring(0, index)
    const match = cellData.substring(index, index + searchValue.length)
    const end = cellData.substring(index + searchValue.length)
    return (
      <>
        {begin}
        <HighlightedValue>{match}</HighlightedValue>
        {end}
      </>
    )
  }
}

function stockWithUnit({ cellData, rowData }: TableCellProps) {
  return `${cellData} ${rowData.wareUnit.code}`
}
