import React from 'react'

import { WareCategoryTypeEnum } from '../../../graph/generated'
import { GMenuCategoryForm } from '../../../graph/types'
import { MenuBaseFormLoader } from '../atoms/MenuBaseFormLoader'
import { useMenu } from '../hooks/useMenu'
import { MenuCategoryFormQuery } from './CategoryForm.graphql'

interface IProps {
  categoryId: Maybe<ID>
  categoryType: WareCategoryTypeEnum
  children: ReactNode
}

export type TCategoryFormValues = GMenuCategoryForm.WareCategory

export const CategoryFormLoader: React.FC<IProps> = ({
  categoryId,
  categoryType,
  children,
}) => {
  const menu = useMenu()
  return (
    <MenuBaseFormLoader<TCategoryFormValues>
      isCreate={categoryId === null}
      onCreate={values => menu.category.createCategory(values, categoryType)}
      onUpdate={values => menu.category.updateCategory(values, categoryType)}
      getDefaultValues={() => getDefaults(categoryType)}
      loadValues={async () => {
        const { data } = await menu.client.query<GMenuCategoryForm.Query>({
          query: MenuCategoryFormQuery,
          variables: { categoryId },
          fetchPolicy: 'no-cache',
        })
        return data.wareCategory
      }}
    >
      {children}
    </MenuBaseFormLoader>
  )
}

function getDefaults(categoryType: WareCategoryTypeEnum): TCategoryFormValues {
  return {
    id: 'NEW',
    visible: true,
    internalNoteLabel: '',
    pictogram: '',
    nameLabel: '',
    categoryType: { enum: categoryType },
    recipes: [],
  }
}
