import { observer } from 'mobx-react-lite'
import React from 'react'
import { useQuery } from 'react-apollo-hooks'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { Headline } from '../../../atoms/Headline'
import { useRoot } from '../../../atoms/RootProvider'
import {
  useMMenuCategoryVisibility,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import { GCategoryList } from '../../../graph/types'
import { useMutationGuard } from '../../../hooks/useMutationGuard'
import { getCategoryRoute } from '../models/MenuCategoryModel'
import { CategoryListTable } from '../molecules/CategoryListTable'
import { CreateButton } from '../molecules/CreateButton'
import { CategoryListQuery } from './CategoryList.graphql'

interface IProps {
  headline: string
  subHeadline?: string
  categoryType: WareCategoryTypeEnum
  heightStretch?: boolean
}

const CategoryList: React.FC<IProps> = ({
  headline,
  subHeadline = false,
  categoryType,
}) => {
  const { settings } = useRoot()
  const { data, loading, updateQuery, refetch } = useQuery<
    GCategoryList.Query,
    GCategoryList.Variables
  >(CategoryListQuery, {
    ...settings.getQueryProps({ categoryType }),
    suspend: false,
  })

  const changeVisibility = useMutationGuard(useMMenuCategoryVisibility())

  const categories = (data && data.categories) || []

  return (
    <>
      <FlexRowSingle>
        <FlexColumn widthGrowIndex={1}>
          <Headline>{headline}</Headline>
          {subHeadline && <Headline size="smaller">{subHeadline}</Headline>}
        </FlexColumn>
        <FlexColumn fixedWidth={true}>
          <CreateButton routeTo={getCategoryRoute('create', categoryType)} />
        </FlexColumn>
      </FlexRowSingle>
      <CategoryListTable
        isLoading={loading}
        categories={categories}
        onToggle={(categoryId: ID, visible: boolean) => {
          changeVisibility(
            { categoryId, visible },
            {
              optimisticResponse: {
                updateWareCategory: {
                  __typename: 'WareCategory',
                  id: categoryId,
                  visible,
                },
              },
            },
          )
        }}
        isSortable={true}
        showHeader={false}
        fullHeight={true}
        showVisibilityColumn={true}
        onSort={async ({ item, items, offset }) => {
          updateQuery(() => ({
            categories: items,
          }))
          const result = await settings.menu.category.sortCategory(
            item.id,
            offset,
          )
          if (result) {
            await refetch()
          }
          return result
        }}
      />
    </>
  )
}

export default observer(CategoryList)
