import { LocationDescriptor } from 'history'
import React, { MouseEvent } from 'react'
import styled from 'styled-components'

import { RelativeLink } from '../../../atoms/RelativeLink'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'

interface IProps {
  routeTo?: LocationDescriptor
  onClick?: (event: MouseEvent<HTMLAnchorElement>) => void
}

const CreateButtonContainer = styled.div`
  position: absolute;
  right: 0;
  z-index: 5;
`

export const CreateButton: React.FC<IProps> = ({
  routeTo = 'create',
  onClick,
}) => (
  <CreateButtonContainer>
    <RelativeLink to={routeTo} onClick={onClick}>
      <TouchButton
        iconName="add_circle"
        color={Colors.green}
        width={Spacing.ButtonHeight}
        height={Spacing.ButtonHeight}
      />
    </RelativeLink>
  </CreateButtonContainer>
)
