import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GMenuRecipeList } from '../../../graph/types'
import { MenuRecipeFragment } from '../models/MenuRecipeModel.graphql'

export const MenuRecipeListQuery = gql`
  query GMenuRecipeList(
    $branchId: ID!
    $categoryId: ID
    $categoryType: WareCategoryTypeEnum
  ) {
    recipes(
      companyBranch: $branchId
      wareCategory: $categoryId
      wareCategoryType: $categoryType
    ) {
      ...GMenuRecipe
    }
  }
  ${MenuRecipeFragment}
`

export const QueryRecipeList = buildQuery<
  GMenuRecipeList.Query,
  GMenuRecipeList.Variables
>(MenuRecipeListQuery)
