import { useStateRef } from '@speedlo/hooks'
import React, { useCallback } from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import {
  ISelectControlProps,
  SelectControl,
} from '../../../atoms/SelectControl'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps<OptionType> extends ISelectControlProps<OptionType> {
  onAdd(option: OptionType): void
  addLabel?: string
  addButtonText?: string
}

export function MenuSuggestionPanel<
  OptionType extends { id: ID; nameLabel: string }
>(props: IProps<OptionType>) {
  const {
    onAdd,
    addLabel = i18n.t`Add`,
    addButtonText = i18n.t`Add`,
    ...selectProps
  } = props

  const [getSelected, setSelected] = useStateRef<Maybe<OptionType>>(null)

  const onConfirm = useCallback(
    () => {
      const selected = getSelected()
      if (selected !== null) {
        onAdd(selected)
        setSelected(null)
      }
    },
    [onAdd],
  )

  const onKeyDown = useCallback((ev: React.KeyboardEvent<HTMLElement>) => {
    if (ev.key === 'Enter') {
      onConfirm()
    }
  }, [])

  const onSelect = useCallback(option => {
    setSelected(option || null)
  }, [])

  return (
    <FlexRowSingle justify="flex-start">
      <FlexColumn fixedWidth={true} width="4rem" margin="auto 0">
        {addLabel}
      </FlexColumn>
      <FlexColumn fixedWidth={true} width="20rem" margin="auto 0">
        <SelectControl<OptionType>
          value={getSelected()}
          onChange={onSelect}
          onKeyDown={onKeyDown}
          isMulti={false}
          isClearable={false}
          getOptionValue={RM.prop('id')}
          getOptionLabel={RM.prop('nameLabel')}
          {...selectProps}
        />
      </FlexColumn>
      <FlexColumn fixedWidth={true} width="10rem" margin="auto 0">
        <TouchButton
          text={addButtonText}
          disabled={getSelected() === null}
          onClick={onConfirm}
        />
      </FlexColumn>
    </FlexRowSingle>
  )
}
