import React from 'react'

import { WareCategoryTypeEnum } from '../../../graph/generated'
import { GMenuRecipe } from '../../../graph/types'
import { WithSettings } from '../../settings/atoms/WithSettings'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { QueryRecipeList } from './RecipeList.graphql'

type TRecipe = GMenuRecipe.Fragment

interface IProps {
  categoryType?: WareCategoryTypeEnum
  onAdd(recipe: TRecipe): void
  filterRecipes(recipes: ReadonlyArray<TRecipe>): TRecipe[]
}

export const RecipeSuggestionPanel: React.FC<IProps> = ({
  categoryType,
  filterRecipes,
  onAdd,
}) => (
  <WithSettings
    observe={true}
    render={settings => (
      <QueryRecipeList
        {...settings.getQueryProps({ categoryType })}
        fetchPolicy="cache-and-network"
        render={({ recipes }) => (
          <MenuSuggestionPanel<TRecipe>
            options={filterRecipes(recipes)}
            onAdd={onAdd}
          />
        )}
      />
    )}
  />
)
