import { observer, useComputed } from 'mobx-react-lite'
import React from 'react'
import { useQuery } from 'react-apollo-hooks'

import { useRoot } from '../../../atoms/RootProvider'
import { GMenuRecipeList } from '../../../graph/types'
import { isPartialMatch } from '../../../helpers/filter'
import { RM } from '../../../helpers/ramda'
import { RecipeListTable } from '../molecules/RecipeListTable'
import { MenuRecipeListQuery } from './RecipeList.graphql'

export const RecipeListLoader = observer(() => {
  const { settings } = useRoot()
  const { data, loading } = useQuery<
    GMenuRecipeList.Query,
    GMenuRecipeList.Variables
  >(MenuRecipeListQuery, {
    ...settings.getQueryProps({
      categoryId: settings.menu.recipe.categoryFilterId,
    }),
    suspend: false,
    fetchPolicy: 'cache-and-network',
  })

  const filteredRecipes = useComputed(
    () => {
      if (!data || !data.recipes || data.recipes.length === 0) {
        return []
      }
      const partialMatch = isPartialMatch(settings.menu.recipe.filterInput)
      return data.recipes.filter(
        RM.cond([
          [RM.propSatisfies(partialMatch, 'code'), RM.T],
          [RM.propSatisfies(partialMatch, 'nameLabel'), RM.T],
        ]),
      )
    },
    [data && data.recipes],
  )

  return (
    <RecipeListTable
      recipes={filteredRecipes}
      onToggle={settings.menu.recipe.changeVisibility}
      isLoading={loading}
      heightStretch={true}
      showTypeColumn={true}
    />
  )
})
