import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GAllergens } from '../../../graph/types'
import { AllergenFragment } from '../graph/AllergenFragment.graphql'

export const AllergensQuery = gql`
  query GAllergens {
    allergens {
      ...GAllergen
    }
  }
  ${AllergenFragment}
`

export const QueryAllergens = buildQuery<
  GAllergens.Query,
  GAllergens.Variables
>(AllergensQuery)
