import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GStockRecipes } from '../../../graph/types'

export const StockRecipesQuery = gql`
  query GStockRecipes($stockId: ID!) {
    stockItem(id: $stockId) {
      id
      ingredients {
        id
        recipe {
          id
          nameLabel
          descriptionLabel
          recipeType: wareCategoryType {
            enum
          }
        }
      }
    }
  }
`

export const QueryStockRecipes = buildQuery<
  GStockRecipes.Query,
  GStockRecipes.Variables
>(StockRecipesQuery)
