import { observer } from 'mobx-react-lite'
import React from 'react'
import { useQuery } from 'react-apollo-hooks'

import { useRoot } from '../../../atoms/RootProvider'
import { WareCategoryTypeEnum } from '../../../graph/generated'
import { GCategoryList, GMenuCategoryListItem } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { CategoryListQuery } from './CategoryList.graphql'

interface IProps {
  categoryType: WareCategoryTypeEnum
  onAdd(recipe: TCategory): void
  addButtonText: string
  currentCategories: TCategory[]
}

type TCategory = GMenuCategoryListItem.Fragment

const CategorySuggestionPanel: React.FC<IProps> = ({
  categoryType,
  currentCategories,
  addButtonText,
  onAdd,
}) => {
  const { settings } = useRoot()
  const { data, loading } = useQuery<
    GCategoryList.Query,
    GCategoryList.Variables
  >(CategoryListQuery, {
    ...settings.getQueryProps({ categoryType }),
    fetchPolicy: 'cache-and-network',
    suspend: false,
  })

  const categories = (data && data.categories) || []

  return (
    <MenuSuggestionPanel<TCategory>
      isLoading={loading}
      options={RM.without(currentCategories, categories)}
      addButtonText={addButtonText}
      onAdd={onAdd}
    />
  )
}

export default observer(CategorySuggestionPanel)
