import { observer, useComputed } from 'mobx-react-lite'
import React from 'react'
import { useQuery } from 'react-apollo-hooks'

import { useRoot } from '../../../atoms/RootProvider'
import { GMenuStockList } from '../../../graph/types'
import { isPartialMatch } from '../../../helpers/filter'
import { RM } from '../../../helpers/ramda'
import { MenuStockListQuery } from './StockList.graphql'
import { StockListTable } from './StockListTable'

export const StockListLoader = observer(() => {
  const { settings } = useRoot()
  const { data, loading } = useQuery<
    GMenuStockList.Query,
    GMenuStockList.Variables
  >(MenuStockListQuery, {
    ...settings.queryProps,
    suspend: false,
  })

  const filteredItems = useComputed(
    () => {
      if (!data || !data.stockItems || data.stockItems.length === 0) {
        return []
      }
      const searchValue = settings.menu.stock.filterInput
      const partialMatch = isPartialMatch(searchValue)
      return data.stockItems.filter(
        RM.cond([
          [RM.propEq('plu', searchValue), RM.T],
          [RM.propSatisfies(partialMatch, 'code'), RM.T],
          [RM.propSatisfies(partialMatch, 'nameLabel'), RM.T],
        ]),
      )
    },
    [data && data.stockItems],
  )

  return <StockListTable stockItems={filteredItems} isLoading={loading} />
})
