import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GMenuStockForm } from '../../../graph/types'
import { AllergenFragment } from '../graph/AllergenFragment.graphql'
import { WareUnitFragment } from '../graph/WareUnitFragment.graphql'

export const MenuStockFormQuery = gql`
  query GMenuStockForm($stockId: ID!) {
    stockItem(id: $stockId) {
      id
      code
      plu
      note
      nameLabel: label
      allowNegative
      wareUnit {
        ...GWareUnit
      }
      allergens {
        ...GAllergen
      }
      companyBranch {
        id
        allowNegative: stockItemsCanBeNegative
      }
    }
  }
  ${AllergenFragment}
  ${WareUnitFragment}
`

export const MenuStockDefaultsQuery = gql`
  query GMenuStockDefaults($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      allowNegative: stockItemsCanBeNegative
    }
  }
`

export const QueryMenuStock = buildQuery<
  GMenuStockForm.Query,
  GMenuStockForm.Variables
>(MenuStockFormQuery)
