import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { Card } from '../../../atoms/Card'
import { Headline } from '../../../atoms/Headline'
import { WareCategoryTypeEnum } from '../../../graph/generated'
import { FullPageLayout } from '../../../molecules/FullPageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { TopRightButtonContainer } from '../../../molecules/TopRightButtonContainer'
import { getCategoryTypeLabel } from '../labels'
import { SubmitButton } from '../molecules/SubmitButton'
import { CategoryBasicForm } from '../organisms/CategoryFormBasic'
import { CategoryFormLoader } from '../organisms/CategoryFormLoader'
import { CategoryRecipesView } from '../organisms/CategoryRecipesView'

interface IProps {
  categoryId: Maybe<ID>
  categoryType: WareCategoryTypeEnum
}

export const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
`
export const FormFixedRow = styled.div`
  flex: 0 0 8rem;
`
export const FormStretchedRow = styled.div`
  flex: 1 1 0;
  min-height: 0;
  position: relative;
`

export const MenuCategoryPage: React.FC<IProps> = ({
  categoryId,
  categoryType,
}) => (
  <CategoryFormLoader categoryId={categoryId} categoryType={categoryType}>
    <>
      <FullPageLayout
        pageTitle={getCategoryTypeLabel(categoryType)}
        hideLogoutWidget={true}
        useBackButton={true}
      />

      <PageLayoutFullHeightItem>
        <FormContainer>
          <FormFixedRow>
            <Card>
              <CategoryBasicForm categoryType={categoryType} />
            </Card>
          </FormFixedRow>
          <FormStretchedRow>
            <Card stretchToParent={true}>
              <Headline>
                <Trans>Recipes assigned to this category</Trans>
              </Headline>
              <CategoryRecipesView categoryType={categoryType} />
            </Card>
          </FormStretchedRow>
        </FormContainer>
      </PageLayoutFullHeightItem>

      <TopRightButtonContainer>
        <CanMutate
          mutationName={
            categoryId === null ? 'createWareCategory' : 'updateWareCategory'
          }
        >
          <SubmitButton isCreate={categoryId === null} />
        </CanMutate>
      </TopRightButtonContainer>
    </>
  </CategoryFormLoader>
)
