import React from 'react'
import { Redirect, Route, RouteComponentProps, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { MenuCategoryListPage } from '../pages/MenuCategoryListPage'
import { MenuCategoryPage } from '../pages/MenuCategoryPage'
import { MenuRecipeListPage } from '../pages/MenuRecipeListPage'
import { MenuRecipePage } from '../pages/MenuRecipePage'
import { MenuStockDepositPage } from '../pages/MenuStockDepositPage'
import { MenuStockListPage } from '../pages/MenuStockListPage'
import { MenuStockPage } from '../pages/MenuStockPage'

interface IProps extends RouteComponentProps<{}> {}

export const MenuRoutes: React.FC<IProps> = ({ match }) => (
  <Switch>
    <SecureRoute
      exact={true}
      path={match.path}
      render={() => <Redirect to={`${match.path}/category`} />}
    />
    <SecureRoute
      exact={true}
      path={`${match.path}/category`}
      component={MenuCategoryListPage}
    />
    <SecureRoute
      path={`${match.path}/category/:categoryId`}
      render={({ match: { params }, location: { state } }) => (
        <MenuCategoryPage
          categoryId={params.categoryId === 'create' ? null : params.categoryId}
          categoryType={state.categoryType}
        />
      )}
    />
    <SecureRoute
      exact={true}
      path={`${match.path}/recipe`}
      component={MenuRecipeListPage}
    />
    <SecureRoute
      path={`${match.path}/recipe/:recipeId`}
      render={({ match: { params }, location: { state } }) => (
        <MenuRecipePage
          recipeId={params.recipeId === 'create' ? null : params.recipeId}
          recipeType={state.recipeType}
        />
      )}
    />
    <SecureRoute
      exact={true}
      path={`${match.path}/stock`}
      component={MenuStockListPage}
    />
    <SecureRoute
      path={`${match.path}/stock/deposit`}
      component={MenuStockDepositPage}
    />
    <SecureRoute
      path={`${match.path}/stock/:stockId`}
      render={({ match: { params } }) => (
        <MenuStockPage
          stockId={params.stockId === 'create' ? null : params.stockId}
        />
      )}
    />
    <Route component={NotFoundPage} />
  </Switch>
)

export default MenuRoutes
