import { Trans } from '@lingui/macro'
import { Dialog, DialogTitle } from '@material-ui/core'
import React, { Component } from 'react'

import { RelativeRedirect } from '../../../atoms/RelativeRedirect'
import { WareCategoryTypeEnum } from '../../../graph/generated'
import { TouchButton } from '../../../molecules/TouchButton'
import { getRecipeRoute } from '../models/MenuRecipeModel'
import { CreateButton } from './CreateButton'

interface IProps {}

interface IState {
  isOpen: boolean
  recipeType: Maybe<WareCategoryTypeEnum>
}

export class RecipeTypeSelection extends Component<IProps, IState> {
  state = { isOpen: false, recipeType: null }
  onClose = () => {
    this.setState({ isOpen: false })
  }
  onOpen = () => {
    this.setState({ isOpen: true })
  }
  onSelect = (recipeType: WareCategoryTypeEnum) => () => {
    this.setState({ isOpen: true, recipeType })
  }
  render() {
    const { isOpen, recipeType } = this.state
    return (
      <>
        {recipeType !== null && (
          <RelativeRedirect
            to={getRecipeRoute('create', recipeType)}
            push={true}
          />
        )}
        <CreateButton
          onClick={ev => {
            ev.preventDefault()
            this.onOpen()
          }}
        />
        <Dialog open={isOpen} onClose={this.onClose}>
          <DialogTitle>Select item type</DialogTitle>
          <TouchButton onClick={this.onSelect(WareCategoryTypeEnum.Main)}>
            <Trans>Ware</Trans>
          </TouchButton>
          <TouchButton onClick={this.onSelect(WareCategoryTypeEnum.SideDish)}>
            <Trans>Sidedish</Trans>
          </TouchButton>
          <TouchButton onClick={this.onSelect(WareCategoryTypeEnum.Cover)}>
            <Trans>Cover</Trans>
          </TouchButton>
        </Dialog>
      </>
    )
  }
}
